<template>
  <div class="modal" id="modal-option" tabindex="-1" role="dialog">
    <div class="modal-dialog" role="document">
      <div class="modal-content">

        <div class="modal-header">
            <p class="modal-title">以下から選択</p>
            <button type="button" class="close" @click="modal_dismiss" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
        </div>

        <div class="modal-body">
          <ul class="text-center">
            <li class="btn-outline-mine btn-lg btn-block" v-for="(option, index) in options" 
            :key="index" v-on:click="$emit('show_answer', option.split(',')[2])">

              {{ option.split(",")[2] + "駅"}}

            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
   props: ["options"],
   name:"modal-option",
   methods:{
     modal_dismiss(){
       document.getElementById("modal-option").style.display="none";
     }
   }
}
</script>
<style>
.text-center{
  padding:0;
}
  #modal-option .btn-block {
    margin: auto 0 8px;
  }
</style>





