<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}
.logo{
  color:#027522!important;

}
a{
  color:#027522;
}
a:hover{
  text-decoration: none;
}

.btn-mine{
    background-color: #027522!important;
    color:white!important;
}

.btn-mine:hover, .btn-mine:focus, 
.btn-mine:active, .btn-mine:active:focus, .btn-mine:active:hover, .btn-mine:active.focus,
.btn-mine.active, .btn-mine.active:focus, .btn-mine.active:hover, .btn-mine.active.focus,
.open > .dropdown-toggle.btn-mine, 
.open > .dropdown-toggle.btn-mine:hover,
.open > .dropdown-toggle.btn-mine:focus, 
.open > .dropdown-toggle.btn-mine.focus,
.btn-mine.disabled:hover, .btn-mine[disabled]:hover, fieldset[disabled] .btn-mine:hover,
.btn-mine.disabled:focus, .btn-mine[disabled]:focus, fieldset[disabled] .btn-mine:focus,
.btn-mine.disabled.focus, .btn-mine[disabled].focus, fieldset[disabled] .btn-mine.focus {
    background: #014916; 
    color: white;
    cursor:pointer;
}

.btn-mine.outline {
    border: 3px solid #027522;
    color: white;
}

.btn-outline-mine{
  color:#027522;
  background-color: #fff;
  border: 2px solid #027522; 
  cursor: pointer;
}

</style>



